<template>
  <breadcrumb-list :items="breadcrumbs">
    <router-link
      :to="{ name: 'template.create' }"
      class="font-medium text-white btn bg-info hover:bg-info-focus focus:bg-info-focus active:bg-info-focus/90"
    >
      <i class="fa fa-plus fa-sm fa-fw"></i>
      {{ $t("labels.add") }}
    </router-link>
  </breadcrumb-list>

  <data-table :titles="titles" :repository="repository">
    <template #filters>
      <div class="py-3">
        <div class="grid grid-cols-1">
          <div class="block">
            <label for="textSearch">{{ $t("attributes.name") }}</label>
            <div class="w-full mt-1.5">
              <input
                v-model="repository.filters.search"
                id="textSearch"
                type="text"
                :placeholder="$t('placeholder.search')"
                class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              />
            </div>
          </div>
        </div>
      </div>
    </template>

    <template #body>
      <tr v-for="template in repository.getItems()" :key="template.id">
        <td class="font-medium text-slate-700 dark:text-navy-100">
          {{ template.id }}
        </td>
        <td class="font-medium text-slate-700 dark:text-navy-100">
          <span>{{ template.user_id }}</span>
        </td>
        <td class="font-medium text-slate-700 dark:text-navy-100">
          <span>{{ template.name }}</span>
        </td>
        <td class="font-medium text-slate-700 dark:text-navy-100">
          <span>{{ template.description }}</span>
        </td>
        <td>
          <div class="flex space-x-2">
            <router-link
              :to="{ name: 'template.edit', params: { id: template.id } }"
              :title="$t('labels.edit')"
              class="h-10 w-10 rounded-full p-0 font-medium text-white btn bg-primary hover:bg-primary-focus focus:bg-primary-focus active:bg-primary-focus/90 dark:bg-accent dark:hover:bg-accent-focus dark:focus:bg-accent-focus dark:active:bg-accent/90"
            >
              <v-icon name="pencil-square" size="5"></v-icon>
            </router-link>

            <v-button
              color="danger"
              :title="$t('labels.delete')"
              :size="10"
              icon
              class="rounded-full p-0"
              @click="deleteModal(template.id)"
            >
              <v-icon name="archive-delete" size="5"></v-icon>
            </v-button>

            <router-link
              :to="{ name: 'template.show', params: { id: template.id } }"
              :title="$t('labels.preview')"
              class="h-10 w-10 rounded-full p-0 font-medium text-white btn bg-info hover:bg-info-focus focus:bg-info-focus active:bg-info-focus/90"
            >
              <v-icon name="document-text" size="5"></v-icon>
            </router-link>
          </div>
        </td>
      </tr>
    </template>
  </data-table>

  <div class="mt-6 grid gap-8 lg:grid-cols-2 2xl:grid-cols-3">
    <div
      v-for="(template, index) in templates"
      :key="index"
      class="rounded border border-slate-200"
    >
      <div
        class="grid gap-4 overflow-hidden p-4 h-[500px] template-preview pointer-events-none"
      >
        <div
          v-for="(question, index) in template.getRenderAbleQuestions()"
          :key="index"
          class="p-4 card"
        >
          <survey-input
            :template="template"
            :question="question"
          ></survey-input>
        </div>
      </div>

      <div
        class="flex w-full items-center justify-between rounded-b border-t border-slate-200 bg-white p-3 py-4"
      >
        <div>
          <p class="text-xl font-semibold line-clamp-1" :title="template.name">
            {{ template.name }}
          </p>
          <p class="text-xs line-clamp-1 mt-1">{{ template.description }}</p>
        </div>

        <div class="inline-flex gap-2">
          <router-link
            :to="{ name: 'template.edit', params: { id: template.id } }"
            :title="$t('labels.edit')"
            class="h-10 w-10 rounded-full p-0 font-medium text-white btn bg-primary hover:bg-primary-focus focus:bg-primary-focus active:bg-primary-focus/90 dark:bg-accent dark:hover:bg-accent-focus dark:focus:bg-accent-focus dark:active:bg-accent/90"
          >
            <v-icon name="pencil-square" size="5"></v-icon>
          </router-link>

          <v-button
            color="danger"
            :title="$t('labels.delete')"
            :size="10"
            icon
            class="rounded-full p-0"
            @click="deleteModal(template.id)"
          >
            <v-icon name="archive-delete" size="5"></v-icon>
          </v-button>

          <router-link
            :to="{ name: 'template.show', params: { id: template.id } }"
            :title="$t('labels.preview')"
            class="h-10 w-10 rounded-full p-0 font-medium text-white btn bg-info hover:bg-info-focus focus:bg-info-focus active:bg-info-focus/90"
          >
            <v-icon name="document-text" size="5"></v-icon>
          </router-link>
        </div>
      </div>
    </div>
  </div>

  <v-modal ref="modal">
    <template #title>{{ $t("modal.titles.delete") }}</template>
    <template #body>{{ $t("modal.messages.delete") }}</template>
    <template #buttons>
      <v-button color="danger" @click="deleteData">
        {{ $t("labels.delete") }}
      </v-button>
    </template>
  </v-modal>
</template>

<script>
import FilterRepository from "@/core/FilterRepository";
import api from "@/core/Api";
import Toast from "@/core/Toast";
import Template from "@/models/Template";
import SurveyInput from "@/components/SurveyInputs/SurveyInput.vue";

export default {
  name: "TemplateList",
  components: { SurveyInput },
  data() {
    return {
      breadcrumbs: [{ title: this.$t("titles.templates"), active: true }],
      titles: [
        { column: "id", title: "#", sortable: true },
        {
          column: "user_id",
          title: this.$t("attributes.user_id"),
        },
        {
          column: "name",
          title: this.$t("attributes.template"),
          sortable: true,
        },
        {
          column: "description",
          title: this.$t("attributes.description"),
        },
        { title: this.$t("labels.actions") },
      ],
      repository: new FilterRepository("/templates", {
        search: "",
      }),
      delete: null,
    };
  },
  methods: {
    deleteModal(id) {
      this.delete = id;
      this.$refs.modal.toggle();
    },
    deleteData() {
      api
        .post(`/template/delete/${this.delete}`)
        .then(() => {
          Toast.fire(this.$t("response.success.delete"), "success");
          this.$refs.modal.toggle();
          this.repository.fetch();
        })
        .catch((error) => {
          Toast.fire(
            this.$t("response.errors." + error.response.status),
            "error"
          );
        });
    },
  },
  created() {
    this.repository.fetch();
  },
  computed: {
    templates() {
      let templates = [];

      if (this.repository.getItems()) {
        this.repository.getItems().forEach((item) => {
          templates.push(new Template(item));
        });
      }

      return templates;
    },
  },
};
</script>

<style>
.filter-panel {
  @apply lg:max-w-lg xl:max-w-2xl;
}
</style>
